import styled from '@emotion/styled';
import {FC, useState} from 'react';
import {getMessageImageUrlFromMessage, Message} from '../assets/messages';

const ModalContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalBackground = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.7;

  cursor: pointer;
`;

const Modal = styled.div`
  position: relative;
  width: 700px;
  height: 750px;
  z-index: 2;
  overflow: hidden;
`;

const ModalBody = styled.div`
  background-color: white;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  padding: 30px 20px;

  display: flex;
  flex-direction: column;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 12px;
  right: 15px;
  font-family: monospace !important;
`;

const MessageImageContainer = styled.div`
  height: 300px;
  margin-bottom: 20px;
  text-align: center;
`;
const MessageImageLink = styled.a`
  display: inline-block;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const MessageImage = styled.img`
  max-width: 100%;
  max-height: 100%;
`;

const MessageBody = styled.div`
  display: flex;
  flex: 1;
  position: relative;
  overflow: hidden;
`;

const MessageText = styled.div`
  overflow-y: auto;
  max-height: 100%;

  text-align: justify;
  align-self: center;
  white-space: pre-line;
  padding: 0 5px;
`;

const MessageName = styled.p`
  text-align: right;
`;

const NavigationButtonContainer = styled.div`
  display: flex;
  justify-content: space-between;
  button {
    text-decoration: underline;
  }
`;

export interface BasicModalProps {
  message: Message;
  onClose?: () => void;
  onNextClick?: () => void;
  onPreviousClick?: () => void;
}

export const StarModal: FC<BasicModalProps> = ({
  message,
  onClose,
  onNextClick,
  onPreviousClick,
}) => {
  const [nsfw, setNsfw] = useState(false);
  const imageUrl = getMessageImageUrlFromMessage(message, false, nsfw);

  return (
    <ModalContainer>
      <ModalBackground onClick={() => onClose?.()} />
      <Modal>
        <ModalBody>
          <CloseButton className="reset" onClick={onClose}>
            X
          </CloseButton>
          <MessageImageContainer>
            <MessageImageLink
              href={imageUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              <MessageImage src={imageUrl} alt="Star" />
            </MessageImageLink>
          </MessageImageContainer>
          {message.sfwHash ? (
            <label style={{maxWidth: 150, margin: 'auto'}}>
              NSFW
              <input type="checkbox" onChange={() => setNsfw((v) => !v)} />
            </label>
          ) : null}
          <MessageBody>
            <MessageText>{message.message.trim()}</MessageText>
          </MessageBody>
          <MessageName> - {message.name}</MessageName>
          <NavigationButtonContainer>
            <button className="reset" onClick={onPreviousClick}>
              &lt; Previous
            </button>
            <button className="reset" onClick={onNextClick}>
              Next &gt;
            </button>
          </NavigationButtonContainer>
        </ModalBody>
      </Modal>
    </ModalContainer>
  );
};
