import {GatsbyStars} from './three/GatsbyStars';

const StarsContainer = styled.div`
  width: 100%;
  height: 100%;
  background: #36135f;
`;

const ListContainer = styled.div`
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
`;

const App: FC = () => {
  const [loading, setLoading] = useState(true);
  if (loading) return <Splash onLoad={() => setLoading(false)} />;

  // return <StarLayoutSim />;

  return (
    <StarsContainer>
      <Canvas camera={{position: [0, 0, 1]}}>
        <GatsbyStars />
      </Canvas>
      <ListContainer>
        <StarList />
      </ListContainer>
    </StarsContainer>
  );

  /* return (
    <Routes>
      <Route
        path="/:star?"
        element={
          <StarsContainer>
            <Canvas camera={{position: [0, 0, 1]}}>
              <GatsbyStars />
            </Canvas>
            <ListContainer>
              <StarList />
            </ListContainer>
          </StarsContainer>
        }
      />
    </Routes>
  ); */
};

export default App;
