import _messages from './messages.json';

export interface Message {
  hash: string;
  message: string;
  name: string;
  sfwHash?: string;
}

export const messages: Message[] = _messages;

export const getMessageImageUrlFromMessage = (
  message: Message,
  thumb = false,
  nsfw = false,
) => {
  const hash = !nsfw && message.sfwHash ? message.sfwHash : message.hash;
  return `${process.env.PUBLIC_URL}/stars${thumb ? '-thumb' : ''}/${hash}.png`;
};
