import styled from '@emotion/styled';
import {useLocalStorage} from '@rehooks/local-storage';
import {FC, useEffect, useMemo, useState} from 'react';
import curvyePaint from '../assets/curvyePaint.gif';
import {getMessageImageUrlFromMessage, messages} from '../assets/messages';

const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
`;

const ProgressBar = styled.div`
  border-color: #4a5052;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: #c0c0c0;
  width: 400px;

  transition: opacity 2s ease-in-out;

  div {
    height: 5px;
    border-radius: 1px;
    color: #f2f0ee;
    background-color: #9c9c9c;

    transition: width 1s ease-in;
  }
`;

const ProgressMessage = styled.h3`
  transition: opacity 2s ease-in-out;
  margin: 0;
`;

const preloadImage = (src: string) =>
  new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = function () {
      resolve(img);
    };
    img.onerror = img.onabort = function () {
      reject(src);
    };
    img.src = src;
  });

interface SplashProps {
  onLoad?: () => void;
}

const loadedImageSet = new Set<string>();
export const Splash: FC<SplashProps> = ({onLoad}) => {
  const [loading, setLoading] = useState(true);
  const [loadedImageCount, setLoadedImageCount] = useState(0);

  const progress = useMemo(
    () => Math.round((loadedImageCount / messages.length) * 100),
    [loadedImageCount],
  );

  const [hasAlreadyBeenLoadedOnce, setHasAlreadyBeenLoadedOnce] =
    useLocalStorage('hasAlreadyBeenLoadedOnce', '');

  useEffect(() => {
    (async () =>
      Promise.all(
        messages.map(async (message) => {
          try {
            await preloadImage(getMessageImageUrlFromMessage(message));
            console.info(`loaded ${message.hash}`);
            await preloadImage(getMessageImageUrlFromMessage(message, true));
            console.info(`loaded ${message.hash}-thumb`);

            if (message.sfwHash) {
              await preloadImage(
                getMessageImageUrlFromMessage(message, false, true),
              );
              console.info(`loaded ${message.sfwHash}`);
              await preloadImage(
                getMessageImageUrlFromMessage(message, true, true),
              );
              console.info(`loaded ${message.sfwHash}-thumb`);
            }
          } catch (err) {
            console.error(err);
          } finally {
            loadedImageSet.add(message.hash);
            setLoadedImageCount(loadedImageSet.size);
          }
        }),
      ))();
  }, []);

  useEffect(() => {
    if (loadedImageCount === messages.length) {
      setLoading(false);
      if (hasAlreadyBeenLoadedOnce) {
        onLoad?.();
      }
      return;
    }
  }, [loadedImageCount, hasAlreadyBeenLoadedOnce, onLoad]);

  return (
    <Container
      style={{cursor: loading ? 'progress' : 'pointer'}}
      onClick={() => {
        if (loading) return;
        setHasAlreadyBeenLoadedOnce('true');
        onLoad?.();
      }}
    >
      <div>
        <img src={curvyePaint} alt="Painting" />
      </div>
      <ProgressBar style={{opacity: progress >= 100 ? 0 : 1}}>
        <div style={{width: `${progress}%`}} />
      </ProgressBar>
      <ProgressMessage style={{opacity: progress >= 100 ? 1 : 0}}>
        Click anywhere to start...
      </ProgressMessage>
    </Container>
  );
};
