import styled from '@emotion/styled';
import useLocalStorage from '@rehooks/local-storage';
import {FC, useEffect, useState} from 'react';
import {Link, useNavigate, useParams} from 'react-router-dom';
import curvyePaint from '../assets/curvyePaint.gif';
import {
  getMessageImageUrlFromMessage,
  Message,
  messages,
} from '../assets/messages';
import TANTIES from '../assets/TANTIES.gif';
import {StarModal} from './StarModal';

const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  gap: 10px;
`;

const ListContainer = styled.div`
  max-width: 1500px;
  display: flex;
  flex-wrap: wrap;

  justify-content: center;
  gap: 5px;
  overflow-y: auto;
`;

const ListItem = styled.div`
  cursor: pointer;
  display: inline-block;
  align-self: center;
  margin: 0 5px;
`;

const StarThumb = styled.img`
  max-width: 100px;
  max-height: 100px;
`;

const SettingButtonContainer = styled.div`
  position: absolute;
  top: 10px;
  left: 10px;
`;

const SettingButton = styled.button`
  margin: 0 5px;
`;

const TitleH1 = styled.h1`
  color: white;
  flex: 0;
  margin: 0;
`;

const Emote = styled.img`
  max-height: 36px;
  margin: 0 5px;
`;

export const StarList: FC = () => {
  const {star} = useParams();
  const navigate = useNavigate();

  const [selectedMessage, setSelectedMessage] = useState<Message>();
  const [viewedMessagesHash, setViewedMessagesHash] = useLocalStorage(
    'viewedMessagesHash',
    '',
  );
  const [viewedMessages, setViewedMessages] = useState<Set<Message>>(() => {
    const _viewedMessages = new Set<Message>();
    for (const hash of viewedMessagesHash.split(',')) {
      const message = messages.find((message) => message.hash === hash);
      if (!message) continue;
      _viewedMessages.add(message);
    }

    return _viewedMessages;
  });

  const [hideViewedMessages, setHideViewedMessages] = useState(false);

  const handleSelectMessage = (message: Message) => {
    setSelectedMessage(message);
  };

  useEffect(() => {
    console.log(star);
    if (!star && selectedMessage) handleResetSelectedMessage();
    const message = messages.find(({hash}) => hash === star);
    if (!message || message.hash === selectedMessage?.hash) return;

    setSelectedMessage(message);
  }, [star]);

  const handleResetSelectedMessage = () => {
    navigate('/');
    setSelectedMessage(undefined);
  };
  const handleSelectRelativeMessage = (index: number) =>
    setSelectedMessage((currentMessage) => {
      if (!currentMessage) return;
      const relativeIndex = messages.indexOf(currentMessage) + index;

      const message =
        relativeIndex >= messages.length
          ? messages.at(relativeIndex - messages.length)
          : messages.at(relativeIndex);

      if (message) {
        navigate(`/${message.hash}`);
      }

      return message;
    });

  const handleResetViewed = () => {
    setViewedMessages(new Set());
    setViewedMessagesHash('');
  };

  useEffect(() => {
    const handleKeydown = ({key}: KeyboardEvent): void => {
      switch (key) {
        case 'Escape':
          return handleResetSelectedMessage();
        case 'ArrowLeft':
          return handleSelectRelativeMessage(-1);
        case 'ArrowRight':
          return handleSelectRelativeMessage(1);
      }
    };

    window.addEventListener('keydown', handleKeydown);
    return () => window.removeEventListener('keydown', handleKeydown);
  }, []);

  useEffect(() => {
    if (!selectedMessage || viewedMessages.has(selectedMessage)) return;

    setViewedMessages((_viewedMessages) => {
      _viewedMessages.add(selectedMessage);
      setViewedMessagesHash(
        Array.from(_viewedMessages)
          .map(({hash}) => hash)
          .join(','),
      );

      return _viewedMessages;
    });
  }, [selectedMessage, setViewedMessagesHash, viewedMessages]);

  const titleEmote = selectedMessage ? (
    <Emote src={TANTIES} />
  ) : (
    <Emote src={curvyePaint} />
  );

  const messageList = hideViewedMessages
    ? messages.filter((message) => !viewedMessages.has(message))
    : messages;

  return (
    <Container>
      <SettingButtonContainer>
        <SettingButton
          onClick={handleResetViewed}
          disabled={viewedMessages.size === 0}
          style={{display: 'none'}}
        >
          Reset viewed ({viewedMessages.size}/{messages.length})
        </SettingButton>
        <SettingButton
          onClick={() => setHideViewedMessages((v) => !v)}
          style={{display: 'none'}}
        >
          {hideViewedMessages ? 'Show' : 'Hide'} viewed
        </SettingButton>
      </SettingButtonContainer>
      <div style={{textAlign: 'center', margin: '10px 0'}}>
        <TitleH1>
          {titleEmote}
          Happy birthday, nerd
          {titleEmote}
        </TitleH1>
      </div>
      <ListContainer style={{overflowY: selectedMessage ? 'hidden' : 'auto'}}>
        {messageList.map((message) => (
          <ListItem
            key={message.hash}
            // onClick={() => handleSelectMessage(message)}
            // onClick={() => navigate('/', {state: {star: message.hash}})}
            // onClick={() => navigate(`/?star=${message.hash}`)}
          >
            <Link to={`/${message.hash}`}>
              <StarThumb
                src={getMessageImageUrlFromMessage(message, true)}
                /* style={{opacity: viewedMessages.has(message) ? 0.2 : 1}} */
                alt="Star"
              />
            </Link>
          </ListItem>
        ))}
      </ListContainer>
      {selectedMessage ? (
        <StarModal
          message={selectedMessage}
          onClose={handleResetSelectedMessage}
          onPreviousClick={() => handleSelectRelativeMessage(-1)}
          onNextClick={() => handleSelectRelativeMessage(1)}
        />
      ) : null}
    </Container>
  );
};
